import HodlDexContract from './contracts/HodlDex.json'
import HodlTContract from './contracts/HodlT.json'

const HODLDEX_ADDR = process.env.REACT_APP_HODLDEX_ADDR
const HODLT_ADDR = process.env.REACT_APP_HODLT_ADDR

const config = {
  COLD_PERIOD_FOR_REFRESHING_PRICING: 60000 * 1, // 1 mins
  DEFAULT_GAS: '800000',
  HodlDex: (web3) => {
    if (!web3) return null
    if (web3.utils) {
      const address = web3.utils.toChecksumAddress(HODLDEX_ADDR)
      if (address) {
        const instance = new web3.eth.Contract(HodlDexContract.abi, address)
        return instance
      } else {
        window.alert('HodlDex contract not deployed to detected network.')
        return { error: true }
      }
    }
  },
  HodlT: (web3) => {
    if (!web3) return null
    if (web3.utils) {
      const address = web3.utils.toChecksumAddress(HODLT_ADDR)
      if (address) {
        const instance = new web3.eth.Contract(HodlTContract.abi, address)
        return instance
      } else {
        window.alert('HodlTContract contract not deployed to detected network.')
        return { error: true }
      }
    }
  },
  etherscanPath: process.env.REACT_APP_ETHERSCAN_ROOT
}

export default config
