import React from "react";
import BigNumber from "bignumber.js";
import { withTranslation } from 'react-i18next';

import { notify } from '../../utils';
import Modal from './Modal';
import history from '../../history';
import web3instance from '../../connector/web3';

const actionMapping = {
  "wallet.balances.card.eth.btn1": "Deposit",
  "wallet.balances.card.eth.btn2": "Withdraw",
  "wallet.balances.card.hodlc.btn1": "Trade",
  "wallet.balances.card.hodlc.btn2": "To HODLT",
  "wallet.balances.card.hodlt.btn1": "Approve",
  "wallet.balances.card.hodlt.btn2": "To HODLC",
}

class BalanceCard extends React.Component {
  state = {
    showModal: false,
    action: null
  };

  showModal = async (e) => {
    console.log(e)
    const btnVal = actionMapping[e.target.getAttribute('value')];
    if (btnVal === 'Trade') {
      history.push('/trade')
    } else {
      if(!window.ethereum) return window.alert("Metamask or Mist is not installed. You should consider trying MetaMask or Mist!")
      const web3 = web3instance(window.web3);
      const accounts = await web3.eth.getAccounts();
      if (accounts.length === 0) {
        notify.success("Connect with Metamask to continue.");
        await window.ethereum.enable()
      } else {
        this.setState({ showModal: true, action: btnVal });
        // history.push(`/wallet/${this.props.type}/${btnVal}`)
      }
    }
  };

  hideModal = () => {
    this.setState({ showModal: false });
    history.push('/wallet')
  };

  renderBalance = () => {
    if (this.props.type === 'eth' || this.props.type === 'hodlt') {
      let a = new BigNumber(this.props.balance);
      let b = a.toFixed(18);
      return b
    } else {
      let a = new BigNumber(this.props.balance);
      let b = a.toFixed(10);
      return b
    }
  }
  render() {
    const { t } = this.props;
    return (
      <div className="balance-card-container">
        <div className={`balance-card ${this.props.type}-card`}>
          <div className="top">
            <img
              alt=""
              src={require(`../../assets/images/${this.props.type}.svg`)}
            ></img>
            <div className="title">{this.props.type.toUpperCase()}</div>
          </div>
          <img
            alt=""
            className="logo-mark"
            src={require(`../../assets/images/${this.props.type}-lg.svg`)}
          ></img>
          <div className="bottom">
            <div className="number">
              <span className="data">{this.props.balance && this.renderBalance()}{" "}</span>
            </div>
            {this.props.type !== "hodlt" ?
              <div className="number">
                <span className="fiat" >
                  ${Number(this.props.USDbalance).toFixed(2)} USD
                </span>
              </div>
              :
              <div className="t-group">
                <div className="number"><b>{Number(this.props.redeemPower || 0).toFixed(4)}</b> {t('wallet.balances.card.hodlt.approveToRedeem')}</div>
              </div>
            }
          </div>

        </div>
        <div className="btn-group">
          <div className="ui fluid button wallet-action-button" value={this.props.button1} onClick={this.showModal}>
            {t(this.props.button1)}
          </div>
          <div className="ui fluid button wallet-action-button" value={this.props.button2} onClick={this.showModal}>
            {t(this.props.button2)}
          </div>
        </div>
        {this.state.showModal && <Modal hideModal={this.hideModal} type={this.props.type} actionName={this.state.action} />}
      </div>

    );
  }
}

export default withTranslation()(BalanceCard);
