import React from "react";
import { connect } from "react-redux";
import connector from "../../connector";
import { buyHodlC, sellHodlC, getDexData, getUserBalances } from "../../actions";
import notify from "../../utils/notify";
import OrderBook from "./OrderBook";
import history from "../../history.js";
import { withTranslation } from 'react-i18next';

const TOTAL_SUPPLY = 20000000;
const MILIION = 1000000;

const menuLink = [
  {
    name: "Global Orders",
    state: true,
    path: "/trade/global",
    key: "global"
  },
  {
    name: "My Orders",
    state: false,
    path: "/trade/myOrders",
    key: "myOrders"
  },
];

class Trade extends React.Component {
  state = {
    HodlcAmount: 0,
    EthAmount: 0,
    show: true,
    curForm: "Buy",
    ethUSD: 0,
    hodlcUSD: 0,
    minUSD: 50,
    maxUSD: 500,
    showGlobal: true,
    mainUnit: "ETH",
    subUnit: "HODLC",
    hide: true
  };

  async componentDidMount() {
    const rate = await connector.getCurrentRate();
    this.props.getUserBalances(window.web3);
    this.props.getDexData();
    this.setState({
      ethUSD: rate.eth,
      hodlcUSD: rate.hodlc,
    });
    if (window.web3) {
      const accounts = await window.web3.eth.getAccounts()
      this.setState({
        hide: !Boolean(accounts[0]),
        EthAmount: this.hodlcToEth(this.state.HodlcAmount),
      });
    }
  }

  hodlcToEth = (hodlc) => {
    if (hodlc === 0) {
      return 0;
    }
    return Number((hodlc * this.state.hodlcUSD) / this.state.ethUSD).toFixed(6);
  };

  async onFormSubmit() {
    const accounts = await window.web3.eth.getAccounts();
    if (accounts.length === 0) return notify.error("Reload the page and connect with Metamask/Mist.");

    if (this.state.HodlcAmount > 0 || this.state.EthAmount > 0) {
      const cur = this.state.curForm;
      if (cur === "Sell") {
        const oneHodlInUsd = await connector.converters.convertHodlToUsd(window.web3, '1')
        let min = 50 / oneHodlInUsd;
        let max = 500 / oneHodlInUsd;
        if (min > this.state.HodlcAmount) return notify.error(this.props.t('trade.notify.sellMin', { min: Number(min).toFixed(4) }))
        if (this.state.HodlcAmount > max) return notify.error(this.props.t('trade.notify.sellMax', { max: Number(max).toFixed(4) }))
        this.props.sellHodlC(this.state.HodlcAmount);
        return notify.success(this.props.t('trade.notify.successSell', { cur: cur.toLowerCase(), hodlc: this.state.HodlcAmount }))
      } else if (cur === "Buy") {
        console.log(this.props.eth, this.state.EthAmount)
        if (this.props.eth < this.state.EthAmount) return notify.error(this.props.t('trade.notify.notEnoughEth', { ethAmount: this.props.eth }))
        const oneEthInUsd = await connector.converters.convertEthToUsd(window.web3, '1')
        let min = 50 / oneEthInUsd;
        let max = 500 / oneEthInUsd;
        if (min > this.state.EthAmount) return notify.error(this.props.t('trade.notify.buyMin', { min: Number(min).toFixed(4) }))
        if (this.state.EthAmount > max) return notify.error(this.props.t('trade.notify.buyMax', { max: Number(max).toFixed(4) }))
        this.props.buyHodlC(this.state.EthAmount);
        return notify.success(this.props.t('trade.notify.successsBuy', { hodlc: this.state.HodlcAmount }))
      } else {
        this.setState({ show: false });
      }
    }
  };

  renderForm = (type) => {
    const form = {
      Buy: {
        valueTarget: "EthAmount",
        convertFunc: (value) => {
          if (value === 0) {
            return 0;
          }
          return Number(
            (value * this.state.ethUSD) / this.state.hodlcUSD
          ).toFixed(6);
        },
        subTarget: "HodlcAmount",
      },
      Sell: {
        valueTarget: "HodlcAmount",
        convertFunc: (value) => {
          if (value === 0) {
            return 0;
          }
          return Number(
            (value * this.state.hodlcUSD) / this.state.ethUSD
          ).toFixed(6);
        },
        subTarget: "EthAmount",
      },
    };
    return (
      <form className="trade-form" onSubmit={this.onFormSubmit.bind(this)}>
        <div className={`ui fluid transparent input main-input ${type}`}>
          <input
            placeholder={this.props.t('trade.form.placeholder')}
            value={this.state[form[type].valueTarget]}
            onChange={(e) => {
              this.setState({
                [form[type].valueTarget]: e.target.value,
                [form[type].subTarget]: form[type].convertFunc(e.target.value),
              });
            }}
          />
          <div className="">{this.state.mainUnit}</div>
        </div>
        <div className="ui fluid transparent input sub-input">
          {this.props.t('trade.form.equivalent')}<small>({this.props.t('trade.form.estimate')})</small>
          <input
            value={
              this.state[form[type].subTarget] === 0
                ? "0"
                : this.state[form[type].subTarget]
            }
            readOnly
          />
          <div className="">{this.state.subUnit}</div>
        </div>
        <div>
          <button className={`ui ${type} fluid button trade-action-button`} type="submit">
            {this.props.t(`trade.form.action.${type.toLowerCase()}`)}
          </button>
        </div>
      </form>
    );
  };

  renderMenu = () => {
    return menuLink.map((ele, id) => {
      let className = "item";
      if (ele.path === this.props.match.path) {
        className = "item active";
      } else if (
        ele.path === "/trade/global" &&
        this.props.match.path === "/trade"
      ) {
        className = "item active";
      }
      return (
        <div className={className} onClick={() => history.push(`${ele.path}`)} key={id}>
          {this.props.t(`trade.orderBook.${ele.key}`)}
        </div>
      );
    });
  };

  connectWithMetaMask = async () => {
    if (window.ethereum) {
      window.ethereum.enable().then(async () => {
        const accounts = await window.web3.eth.getAccounts()
        this.setState({ hide: !Boolean(accounts[0]) })
        this.props.getUserBalances(window.web3);
        this.props.getDexData();
      })
    } else {
      return window.alert(`${this.props.t('trade.alert')}`)
    }
  }

  renderOrders = () => {
    if (
      this.props.match.path === "/trade/global" ||
      this.props.match.path === "/trade"
    ) {
      return (
        <div className="order-section">
          <div className="book sell-book">
            <OrderBook type="sell" />
          </div>
          <div className="book buy-book">
            <OrderBook type="buy" />
          </div>
        </div>
      );
    } else if (this.props.match.path === "/trade/myOrders") {
      return (
        <div className="order-section">
          <div className="book sell-book">
            <OrderBook type="userSell" />
          </div>
          <div className="book buy-book">
            <OrderBook type="userBuy" />
          </div>
        </div>
      );
    }
  };

  render() {
    const { t } = this.props;
    return (
      <div className="trade-container container-fluid">
        <div className="dashboard-session dashboard-market-info">
          <div className="info-card-group">
            <div className="info-card">
              <div className="ui header">Reserve</div>
              <div className="content">{Number(this.props.reserveBalanceHodlc / MILIION).toFixed(2)}M HODLC</div>
            </div>
            <div className="info-card">
              <div className="ui header">Marketcap</div>
              <div className="content">${Number(TOTAL_SUPPLY * this.props.rates / MILIION).toFixed(2)}M</div>
            </div>
          </div><div className="info-card-group">
            <div className="info-card">
              <div className="ui header">Circulation</div>
              <div className="content">{Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION).toFixed(2)}M HODLC (${Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION * this.props.rates).toFixed(2)}M)</div>
            </div>
            <div className="info-card">
              <div className="ui header">HODLC Price</div>
              <div className="content">${Number(this.props.rates).toFixed(4)}</div>
            </div>
          </div>
        </div>
        <div className="trade-info-container">
          <div className={`trade-info-blocker ${this.state.hide}`}>
            <button className="ui button" onClick={this.connectWithMetaMask}>{t('trade.promptToConnet.connect')}<img alt='metamask' className='metamask-icon' src='https://cdn.worldvectorlogo.com/logos/metamask.svg' width='15px' />{t('trade.promptToConnet.startTrade')}</button>
          </div>
          <div className="trade-card trade-info">
            <div className="ui header info-header">{t('trade.info.balances')}</div>
            <div className="info">
              <div className="title">HODLC</div>
              <span>{Number(this.props.hodlc).toFixed(10)}</span>
            </div>
            <div className="info">
              <div className="title">ETH</div>
              <span>{Number(this.props.eth).toFixed(18)}</span>
            </div>
          </div>
          <div className="trade-card trade-action">
            <div className="btn-group">
              <button
                className={`ui fluid button left ${this.state.curForm === "Buy"}`}
                onClick={() =>
                  this.setState({
                    show: true,
                    curForm: "Buy",
                    mainUnit: "ETH",
                    subUnit: "HODLC",
                    EthAmount: 0,
                    HodlcAmount: 0
                  })
                }>{t('trade.switcher.buy')}</button>
              <button
                className={`ui fluid button right ${this.state.curForm === "Sell"}`}
                onClick={() =>
                  this.setState({
                    show: true,
                    curForm: "Sell",
                    mainUnit: "HODLC",
                    subUnit: "ETH",
                    HodlcAmount: 0,
                    EthAmount: 0
                  })
                }
              >
                {t('trade.switcher.sell')}
              </button>
            </div>
            {this.state.show && this.renderForm(this.state.curForm)}
          </div>
        </div>
        <div className="order-book order-switcher">
          <div className="ui secondary menu">{this.renderMenu()}</div>
          <div className="orders-container">{this.renderOrders()}</div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ wallet, trade }) => {
  return {
    eth: wallet.eth,
    hodlc: wallet.hodlc,
    hodlt: wallet.hodlt,
    contractBalanceEth: trade.contractBalanceEth,
    reserveBalanceHodlc: trade.reserveBalanceHodlc,
    rates: trade.rates,
  };
};

export default connect(mapStateToProps, { getUserBalances, buyHodlC, sellHodlC, getDexData })(
  withTranslation()(Trade)
);
