import { GET_USER_BALANCES } from '../actions/types'

const INITIAL_STATE = {
  eth: 0,
  hodlc: 0,
  hodlt: 0,
  ethinUSD: 0,
  hodlcinUSD: 0
}

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_USER_BALANCES:
      return { ...state, ...action.payload }
    default:
      return state
  }
}
