import React from "react";
import { Link } from "react-router-dom";
import { withTranslation } from 'react-i18next';

import SettingModal from '../Settings/modal';

const navItem = [
  {
    tag: 'wallet-page',
    route: '/',
    name: 'wallet'
  },
  {
    tag: 'trade-page',
    route: '/trade',
    name: 'trade'
  },
  {
    tag: 'explorer-page',
    route: '/explorer',
    name: 'explorer'
  }
]

class Navigator extends React.Component {
  state = {
    showModal:false
  }

  hideModal = () => {
    this.setState({showModal:false})
  }
  renderLink = () => {
    const { t } = this.props;
    return navItem.map((ele) => {
      return (
        <Link key={ele.route} to={ele.route} className="item">
          <img alt={ele.tag} src={require(`../../assets/images/${ele.tag}.svg`)} />
          <p className="nav-title">{t(`navigator.${ele.name}`)}</p>
        </Link>
      );
    });
  };

  getCurrentLanguage = () => {
    return localStorage.getItem('i18nextLng');
  }

  render() {
    const { t } = this.props;
    return (
      <div className="side-nav">
        <div className="item-container">
          <div className="top-group">
            <div className="nav-logo">
              <img
                alt="logo"
                src={require("../../assets/images/hodl_badge.svg")}
              ></img>
              <p className="logo-title">HODL DEX</p>
            </div>
            {this.renderLink()}
          </div>
          <div className="bottom-group">
            <div className="item" onClick={()=> this.setState({showModal: true})}>
              <img
                alt="setting"
                src={require(`../../assets/images/setting.svg`)}
              ></img>
              <p className="nav-title">{t(`navigator.settings`)}</p>
            </div>
          </div>
        </div>
        {this.state.showModal&&<SettingModal hideModal={this.hideModal} initialValues={{language:this.getCurrentLanguage()}}/>}
      </div>
    );
  }
}

export default withTranslation()(Navigator);
