import React from 'react'
import { connect } from 'react-redux'
import { depositEth, withdrawEth, getUserBalances } from '../../actions'
import BalanceCard from './BalanceCard'
import EventList from '../EventList'
const TOTAL_SUPPLY = 20000000
const MILIION = 1000000

class Wallet extends React.Component {
  componentDidMount () {
    this.props.getUserBalances()
  }

  render () {
    const { t } = this.props
    return (
      <div className='wallet-container'>
        <div className='container'>
          <div className='dashboard-session dashboard-market-info'>
            <div className='ui header session-header'>{t('marketInfo.title')}</div>
            <div className='info-card-group'>
              <div className='info-card'>
                <div className='ui header'>{t('marketInfo.items.reserve')}</div>
                <div className='content'>{Number(this.props.reserveBalanceHodlc / MILIION).toFixed(2)}M HODLC</div>
              </div>
              <div className='info-card'>
                <div className='ui header'>{t('marketInfo.items.marketcap')}</div>
                <div className='content'>${Number(TOTAL_SUPPLY * this.props.rates / MILIION).toFixed(2)}M</div>
              </div>
            </div><div className='info-card-group'>
              <div className='info-card'>
                <div className='ui header'>{t('marketInfo.items.circulation')}</div>
                <div className='content'>{Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION).toFixed(2)}M HODLC (${Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION * this.props.rates).toFixed(2)}M)</div>
              </div>
              <div className='info-card'>
                <div className='ui header'>{t('marketInfo.items.hodlcPrice')}</div>
                <div className='content'>${Number(this.props.rates).toFixed(4)}</div>
              </div>
            </div>
          </div>
          <div className='dashboard-session'>
            <h2 className='ui header session-header'>{t('wallet.balances.title')}</h2>
            <div className='balance-cards'>
              <BalanceCard type='eth' balance={this.props.eth} USDbalance={this.props.ethinUSD} button1='wallet.balances.card.eth.btn1' button2='wallet.balances.card.eth.btn2' />
              <BalanceCard type='hodlc' balance={this.props.hodlc} USDbalance={this.props.hodlcinUSD} button1='wallet.balances.card.hodlc.btn1' button2='wallet.balances.card.hodlc.btn2' />
              <BalanceCard type='hodlt' balance={this.props.hodlt} redeemPower={this.props.redeemPower} button1='wallet.balances.card.hodlt.btn1' button2='wallet.balances.card.hodlt.btn2' />
            </div>
          </div>
          <div className='dashboard-session'>
            <EventList scope='my' defaultTimes={24} />
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ wallet, trade }) => {
  return {
    eth: wallet.eth,
    hodlc: wallet.hodlc,
    hodlt: wallet.hodlt,
    ethinUSD: wallet.ethinUSD,
    hodlcinUSD: wallet.hodlcinUSD,
    redeemPower: wallet.redeemPower,
    reserveBalanceHodlc: trade.reserveBalanceHodlc,
    rates: trade.rates
  }
}

export default connect(mapStateToProps, { depositEth, withdrawEth, getUserBalances })(Wallet)
