import { CANCEL_BUY, CANCEL_SELL, GET_USER_BALANCES, GET_DEX_DATA } from './types'
import connector from '../connector'

export const depositEth = (amount) => async dispatch => {
  try {
    await connector.depositEth(amount)
  } catch (err) {
    console.error(err)
  }
}

export const withdrawEth = (amount) => async dispatch => {
  try {
    await connector.withdrawEth(amount)
  } catch (err) {
    console.error(err)
  }
}

export const buyHodlC = (amount) => async dispatch => {
  try {
    await connector.buyHodlC(amount)
  } catch (err) {
    console.error(err)
  }
}

export const sellHodlC = (amount) => async dispatch => {
  try {
    console.log(amount)
    await connector.sellHodlC(amount)
  } catch (err) {
    console.error(err)
  }
}

export const cancelBuy = (amount) => async dispatch => {
  try {
    dispatch({
      type: CANCEL_BUY,
      payload: amount
    })
  } catch (err) {
    console.error(err)
  }
}

export const cancelSell = (amount) => async dispatch => {
  try {
    dispatch({
      type: CANCEL_SELL,
      payload: amount
    })
  } catch (err) {
    console.error(err)
  }
}

