import React from "react";
import BigNumber from "bignumber.js";

import config from '../../../connector/config';
import converters from "../../../connector/converters";
import connector from "../../../connector";
import notify from '../../../utils/notify';
import CancelModal from './CancelModel';
import { withTranslation } from 'react-i18next';

const labels = {
  sell: "sell",
  buy: "buy",
  userSell: "sell",
  userBuy: "buy",
};

function convertMoney(amount) {
  const tmp = new BigNumber(amount);
 
  const dollar = tmp
    .dividedBy("1000000000000000000")
    .decimalPlaces(8)
    .toNumber();
  if (isNaN(dollar)) {
    return `$0` 
  } else {
    return `$ ${dollar}`;
  }
}
function convertSellerId(id) {
  if(id){
    const Id = id.slice(0);
    return <span>{Id}</span>;
  }
}
function convertOrderId(id) {
  if(id){
    const Id = id.slice(0);
    return <span>{Id}</span>;
  }
}

const actionName = {
  sell: "sell",
  buy: "buy",
  userSell: "sell",
  userBuy: "sell",
};

class List extends React.Component {
  state = {
    showModal: false,
    orderId: null,
    account: ''
  }

  async componentDidMount() {
    if(window.web3) {
      const accounts = await window.web3.eth.getAccounts()
      this.setState({account: accounts[0]})
    }
  }

  showModal = (id) => {
    this.setState({ 
      showModal: true, 
      orderId: id
     });
  };

  hideModal = () => {
    this.setState({
      showModal: false
    })
  }

  cancelOrder = (id) => {
    if(actionName[this.props.type]){
      connector[`cancel${actionName[this.props.type]}`](id)
      .then(console.log)
      .catch(console.error);
      this.hideModal();
      return notify.success(this.props.t('trade.notify.cancel', {id}))
    }
  };

  renderHodlcBalance = (price) =>  {
    price = converters.ten.less(price);
    return (
      <span>
        {(Math.floor(price * 100) / 100).toFixed(2).split('.')[0]}
        <span className="decimal">.</span>
        <span className='light'>
          {(Math.floor(price * 100) / 100).toFixed(10).split('.')[1]}
        </span>
      </span>
    )
  }

  renderData = (type) => {
    const { orders } = this.props;
    
    if (!orders || orders.length === 0 || orders[0].buyer === "0x0000000000000000000000000000000000000000" || orders[0].seller === '0x0000000000000000000000000000000000000000') {
      return <div className="order">{this.props.t('trade.orderBook.noEvent')}</div>
    }
   
    return orders.map((o) => {   
        return (
          <div
            className={'order'}
            key={o.orderId}
          >
            <div className='ui stackable grid'>
              <div className="three wide column">
                <div className="sm-label">{this.props.t('trade.orderBook.title.orderId')}</div>
                <a
                  href="/"
                  rel="noopener noreferrer"
                  target="_blank"
                  className="order-id"
                >
                  {convertOrderId(o.orderId)}
                </a>
              </div>
              <div className="three wide column">
                <div className="sm-label">{this.props.t(`trade.orderBook.title.${type}`)}</div>
                <a
                  href={`${config.etherscanPath}/address/${o.seller}`}
                  rel="noopener noreferrer"
                  target="_blank"
                  className="seller"
                >
                  {convertSellerId(o.seller)}
                </a>
              </div>
              <div className="four wide column">
                <div className="sm-label">HODLC</div>
                <div className="amount">{this.renderHodlcBalance(o.volumeHodl)}</div>
              </div>
              <div className="three wide column price-row">
                <div className="sm-label">{this.props.t('trade.orderBook.title.price')}</div>
                <div className="price">{convertMoney(o.askUsd)}</div>
              </div>
              <div className={`three wide column action ${o.seller === this.state.account}`}>
                <div className='cancel-your-order-btn ui mini button' onClick={() => this.showModal(o.orderId)}>{this.props.t('trade.orderBook.cancel')}</div>
              </div>
            </div>
          </div>
        );
      
      
    });
  };
renderCount = (amount) => {
 
  if(Number(amount)===1 || Number(amount)===0){
    return this.props.t('trade.orderBook.singleOrder', { amount, units:labels[this.props.type] })
  }else{
    return this.props.t('trade.orderBook.multiOrders', { amount, units:labels[this.props.type] })
  }
}
  render() {
    const t = this.props.t;
    return (
      <div className="list-component">
        <div className="order-count">
          {this.renderCount(this.props.total)}
        </div>
        <div className="order order-header">
          <div className="ui stackable grid">
            <div className="three wide column">
              <div className="order-id">{t('trade.orderBook.title.orderId')}</div>
            </div>
            <div className="three wide column">
              <div className="user">{t(`trade.orderBook.title.${labels[this.props.type]}`)}</div>
            </div>
            <div className="four wide column">
              <div className="amount">HODLC</div>
            </div>
            <div className="three wide column">
              <div className="price">{t('trade.orderBook.title.price')}</div>
            </div>
            <div className="three wide column">
              <div className="price"></div>
            </div>
          </div>
        </div>
        {this.renderData(labels[this.props.type])}
        {this.state.showModal&&<CancelModal hideModal={this.hideModal} cancelOrder={this.cancelOrder} id={this.state.orderId}/>}
      </div>
    );
  }
}

export default withTranslation()(List);
