import React from "react";
import ReactDOM from "react-dom";
import { Field, reduxForm } from "redux-form";
import i18n from 'i18next';
import { withTranslation } from 'react-i18next';

class Index extends React.Component {

  onSubmit = (formValues) => {
    console.log('in form',formValues);
    i18n.changeLanguage(formValues.language);
    this.props.hideModal()
  }
  render() {
    const { t } = this.props;
    return ReactDOM.createPortal(
      <div
        className="ui dimmer modals visible active"
        onClick={() => this.props.hideModal()}
      >
        <div
          onClick={(e) => e.stopPropagation()}
          className="ui small modal visible active main-modal"
        >
          <div className="ui top attached big label modal-title">{t('settings.title')}</div>

          <div className="modal-body">
            <form className="ui form" onSubmit={this.props.handleSubmit(this.onSubmit)}>
              <div className="ui small header">{t('settings.items.language')}</div>
              <Field name="language" component="select">
                <option value="en">English</option>
                <option value="es">Spanish</option>
                <option value="fr">French</option>
                <option value="zh_TW">Traditional Chinese</option>
              </Field>
              <div className="modal-button-group">
              <button
                className="ui button bg-modal-button cancel"
                onClick={() => this.props.hideModal()}
              >
                Cancel
              </button>
              <button
                className="ui button bg-modal-button highlight"
                type="submit"
              >
                Save
              </button>
            </div>
            </form>
          </div>
        </div>
      </div>,
      document.querySelector("#modal")
    );
  }
}



export default reduxForm({
  form: "setting"
})(withTranslation()(Index));
