import config from './config'
import converters from './converters'
import orderBook from './orderBook'
import web3instance from './web3'

// const DEFAULT_GAS = config.DEFAULT_GAS;
const DEFAULT_LOW_GAS = '200000'
const DEFAULT_BUY_GAS = '450000'
const DEFAULT_SELL_GAS = '400000'

const connector = {
  converters,
  getSellBook: orderBook.getSellBook,
  getBuyBook: orderBook.getBuyBook,
  getOrderBook: orderBook.getAll,
  getUserOrders: orderBook.getOne,
  depositEth: async (amountInEther = 2) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const etherInWei = web3.utils.toWei(amountInEther.toString(), 'ether')
      const accounts = await web3.eth.getAccounts()
      return instance.methods.depositEth().send({
        from: accounts[0],
        value: etherInWei
      })
    } catch (err) {
      console.log('[depositEth]', err)
    }
  },
  withdrawEth: async (amountInEther = 0) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const etherInWei = web3.utils.toWei(amountInEther.toString(), 'ether')
      const accounts = await web3.eth.getAccounts()
      return instance.methods.withdrawEth(etherInWei).send({
        from: accounts[0]
      })
    } catch (err) {
      console.log('[withdrawEth]', err)
    }
  },
  getCurrentRate: () => {
    const web3 = web3instance(window.web3)
    try {
      return new Promise(async (resolve, reject) => {
        try {
          const eth = await converters.convertEthToUsd(web3, '1')
          const hodlc = await converters.convertHodlToUsd(web3, '1')
          if (eth && hodlc) {
            resolve({ eth, hodlc })
          }
        } catch (e) {
          reject(e)
        }
      })
    } catch (err) {
      console.log('[getCurrentRate]', err)
    }
  },
  getUserBalances: () => {
    // example return {eth: 0.05, hodlc: 10}
    const web3 = web3instance(window.web3)
    try {
      return new Promise(async (resolve, reject) => {
        const instance = config.HodlDex(web3)
        const hodlTinstance = config.HodlT(web3)
        const hodlcAddr = process.env.REACT_APP_HODLDEX_ADDR
        if (instance && hodlTinstance) {
          try {
            const accounts = await web3.eth.getAccounts()
            const userData = await instance.methods.user(accounts[0]).call()
            const redeemPower = await hodlTinstance.methods.allowance(accounts[0], hodlcAddr).call()
            const hodlTBalance = await hodlTinstance.methods.balanceOf(accounts[0]).call()
            const ethBalance = web3.utils.fromWei(userData.balanceEth.toString(), 'ether')
            const hodlcBalance = converters.ten.less(userData.balanceHodl)
            const wallet = {
              eth: ethBalance,
              ethinUSD: await converters.convertEthToUsd(web3, ethBalance.toString()),
              hodlc: hodlcBalance,
              hodlcinUSD: await converters.convertHodlToUsd(web3, hodlcBalance.toString()),
              hodlt: converters.ten.less(hodlTBalance),
              redeemPower: converters.ten.less(redeemPower)
            }
            resolve(wallet)
          } catch (err) {
            reject(err)
          }
        }
      })
    } catch (err) {
      console.log('[getUserBalances]', err)
    }
  },
  getEvents: (from = 0, to = 'latest', addr = 'my') => {
    const web3 = web3instance(window.web3)
    const TYPES = {
      UserDepositEth: 'deposit',
      UserWithdrawEth: 'withdraw',
      HodlTIssued: 'To HODLT',
      HodlTRedeemed: 'To HODLC',
      BuyHodlC: 'buy HODLC',
      SellHodlC: 'sell HODLC'
    }
    const UNIT = {
      UserDepositEth: 'ETH',
      UserWithdrawEth: 'ETH',
      HodlTIssued: 'HODLT',
      HodlTRedeemed: 'HODLC',
      SellHodlC: 'HODLC',
      BuyHodlC: 'ETH'
    }
    const USER = {
      UserDepositEth: 'user',
      UserWithdrawEth: 'user',
      HodlTIssued: 'user',
      HodlTRedeemed: 'user',
      SellHodlC: 'seller',
      BuyHodlC: 'buyer'
    }
    const AMOUNT = {
      UserDepositEth: (a) => web3.utils.fromWei(a, 'ether'),
      UserWithdrawEth: (a) => web3.utils.fromWei(a, 'ether'),
      HodlTIssued: (a) => converters.ten.less(a),
      HodlTRedeemed: (a) => converters.ten.less(a),
      SellHodlC: (a) => converters.ten.less(a),
      BuyHodlC: (a) => web3.utils.fromWei(a, 'ether')
    }
    return new Promise(async (resolve, reject) => {
      try {
        const history = []
        const instance = config.HodlDex(web3)
        const accounts = await web3.eth.getAccounts()
        const filter = {
          fromBlock: from, toBlock: to, filter: {}
        }
        if (addr === 'my' && accounts.length === 0) return resolve([])
        if (addr === 'my' && accounts.length > 0) {
          filter.filter = {
            user: accounts[0], seller: accounts[0], buyer: accounts[0]
          }
        }
        if (instance) {
          // Event getPastEvents DOCS: https://web3js.readthedocs.io/en/v1.2.7/web3-eth-contract.html#getpastevents
          instance.getPastEvents('UserDepositEth', filter, (err, evts) => { history.push(...evts) })
            .then(() => instance.getPastEvents('UserWithdrawEth', filter, (err, evts) => { history.push(...evts) }))
            .then(() => instance.getPastEvents('HodlTIssued', filter, (err, evts) => { history.push(...evts) }))
            .then(() => instance.getPastEvents('HodlTRedeemed', filter, (err, evts) => { history.push(...evts) }))
            .then(() => instance.getPastEvents('SellHodlC', filter, (err, evts) => { history.push(...evts) }))
            .then(() => instance.getPastEvents('BuyHodlC', filter, (err, evts) => { history.push(...evts) }))
            .then(() => {
              console.log(history)
              const cleanup = []
              let d = {}
              for (const e of history) {
                if (e.event === 'UserDepositEth')console.log(e)
                const userAddr = e.returnValues[USER[e.event]] ? e.returnValues[USER[e.event]].toString() : ''
                d = {
                  type: TYPES[e.event],
                  transactionHash: e.transactionHash,
                  txlink: `${config.etherscanPath}/tx/${e.transactionHash}`,
                  blocklink: `${config.etherscanPath}/block/${e.blockNumber}`,
                  amount: AMOUNT[e.event](e.returnValues['1']),
                  blockNumber: e.blockNumber,
                  unit: UNIT[e.event],
                  userAddr: userAddr,
                  userAddrlink: `${config.etherscanPath}/address/${userAddr}`
                }
                // if(d.type === 'SellHodlC')console.log(e);
                cleanup.push(d)
              }
              const r = cleanup.sort((a, b) => b.blockNumber - a.blockNumber)
              r.total = r.length
              // from latest to oldest
              return resolve(r)
            })
        } else {
          return resolve([])
        }
      } catch (err) {
        console.log('[getEvents]', err)
      }
    })
  },
  buyHodlC: async (amountInEther = 0, lowGas = DEFAULT_LOW_GAS) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const accounts = await web3.eth.getAccounts()
      const etherInWei = web3.utils.toWei(amountInEther.toString(), 'ether')
      return instance.methods.buyHodlC(etherInWei, lowGas).send({
        from: accounts[0],
        gas: DEFAULT_BUY_GAS
      })
    } catch (err) {
      console.log('[buyHodlC]', err)
    }
  },
  sellHodlC: async (amountHodl = 0, lowGas = DEFAULT_LOW_GAS) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const accounts = await web3.eth.getAccounts()
      const amountHodlInMini = converters.ten.more(amountHodl)
      return instance.methods.sellHodlC(amountHodlInMini, lowGas).send({
        from: accounts[0],
        gas: DEFAULT_SELL_GAS
      })
    } catch (err) {
      console.log('[sellHodlC]', err)
    }
  },
  cancelbuy: async (orderId) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const accounts = await web3.eth.getAccounts()
      return instance.methods.cancelBuy(orderId).send({
        from: accounts[0]
      })
    } catch (err) {
      console.log('[cancelbuy]', err)
    }
  },
  cancelsell: async (orderId) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const accounts = await web3.eth.getAccounts()
      return instance.methods.cancelSell(orderId).send({
        from: accounts[0]
      })
    } catch (err) {
      console.log('[cancelsell]', err)
    }
  },
  hodlTIssue: async (amount = 0) => {
    const web3 = web3instance(window.web3)
    try {
      const instance = config.HodlDex(web3)
      const accounts = await web3.eth.getAccounts()
      return instance.methods.hodlTIssue(converters.ten.more(amount)).send({
        from: accounts[0]
      })
    } catch (err) {
      console.log('[hodlTIssue]', err)
    }
  },
  approveHodlT: (amount = 0) => {
    const web3 = web3instance(window.web3)
    return new Promise(async (resolve, reject) => {
      try {
        const hodlTinstance = config.HodlT(web3)
        const accounts = await web3.eth.getAccounts()
        const amnt = converters.ten.more(amount)
        const me = { from: accounts[0] }
        const hodlcAddr = process.env.REACT_APP_HODLDEX_ADDR
        hodlTinstance.methods.approve(hodlcAddr, amnt).send(me)
          .then(() => resolve())
          .catch(err => reject())
      } catch (err) {
        console.log('[approveHodlT]', err)
      }
    })
  },
  hodlTRedeem: (amount = 0) => {
    const web3 = web3instance(window.web3)
    return new Promise(async (resolve, reject) => {
      try {
        const instance = config.HodlDex(web3)
        const accounts = await web3.eth.getAccounts()
        const amnt = converters.ten.more(amount)
        const me = { from: accounts[0] }
        instance.methods.hodlTRedeem(amnt).send(me)
          .then(() => resolve())
          .catch(err => reject())
      } catch (err) {
        console.log('[hodlTRedeem]', err)
      }
    })
  },
  getDexData: async () => {
    const web3 = web3instance(window.web3)
    return new Promise(async (resolve, reject) => {
      try {
        const instance = config.HodlDex(web3)
        const reserveBalanceFromContract = await instance.methods.user(process.env.REACT_APP_HODLDEX_ADDR).call()
        const reserveBalanceHodlc = converters.ten.less(reserveBalanceFromContract.balanceHodl)
        const contractBalanceFromContract = await instance.methods.contractBalanceEth().call()
        const contractBalanceEth = Number(web3.utils.fromWei(contractBalanceFromContract, 'ether')).toFixed(18)
        const ratesFromContract = await instance.methods.rates().call()
        const rates = web3.utils.fromWei(ratesFromContract.hodlUsd, 'ether')
        const result = {
          hodlerCount: await instance.methods.hodlerCount().call(),
          contractBalanceEth: contractBalanceEth,
          distributionsLength: await instance.methods.distributionsLength().call(),
          rates,
          reserveBalanceHodlc: reserveBalanceHodlc
        }
        return resolve(result)
      } catch (err) {
        console.log('[getDexData]', err)
      }
    })
  }
}

export default connector
