import BigNumber from 'bignumber.js'

const ten = {
  more: (amount) => {
    const tmp = new BigNumber(amount)
    const r = tmp.multipliedBy('10000000000').toString()
    return r
  },
  less: (amount) => {
    const tmp = new BigNumber(amount)
    const r = tmp.dividedBy('10000000000').toString()
    return r
  }
}

export default ten
