import React from "react";
import ReactDOM from "react-dom";
import { connect } from "react-redux";
import { notify } from '../../utils';
import { withTranslation } from 'react-i18next';

import {
  depositEth,
  withdrawEth,
  hodlTIssue,
  hodlTRedeem,
  approveHodlT
} from "../../actions";

class Modal extends React.Component {
  state = {
    amount: 0,
    convertAmt: 0
  };

  onSubmit = async () => {
    const { t } = this.props;
    if(!this.state.amount || this.state.amount <= 0) return notify.error(t('wallet.notify.nothing'));
    const { eth, hodlc, hodlt, redeemPower } = this.props;
    if(!this.props.actionName) return;
    if (this.props.type === "eth") {
      if (this.props.actionName === "Deposit") {
        const web3 = window.web3;
        const accounts = await web3.eth.getAccounts();
        if(accounts.length === 0) {
          return notify.error(t('wallet.notify.notConnected'))
        };
        const ethBalanceInWei = await web3.eth.getBalance(accounts[0]);
        const ethBalanceInEther = web3.utils.fromWei(ethBalanceInWei, 'ether');
        const limit = ethBalanceInEther;
        if(Number(this.state.amount) > limit) {
          return notify.error(t('wallet.notify.depositeEthMax', {limit, type:this.props.type.toUpperCase()}))
        }
        this.props.depositEth(this.state.amount);
      } else if(this.props.actionName === "Withdraw") {
        if(Number(this.state.amount) > eth) return notify.error(t('wallet.notify.withdrawEthMax', {eth, type:this.props.type.toUpperCase()}))
        this.props.withdrawEth(this.state.amount);
      }
    } else if (this.props.type === "hodlc" && this.props.actionName === "To HODLT") {
      if(Number(this.state.amount) > hodlc) return notify.error(t('wallet.notify.toHodltMax', { hodlc, type:this.props.type.toUpperCase()}))
      this.props.hodlTIssue(this.state.amount);
    } else if (this.props.type === "hodlt") {
      if (this.props.actionName === "To HODLC") {
        if(Number(this.state.amount) > redeemPower) return notify.error(t('wallet.notify.toHodlcMax', {redeemPower, type:this.props.type.toUpperCase()}))
        this.props.hodlTRedeem(this.state.amount);
      } else if (this.props.actionName === "Approve") {
        const limit = (hodlt - redeemPower);
        if(Number(this.state.amount) > limit) return notify.error(t('wallet.notify.approveMax', {limit, type:this.props.type.toUpperCase()}))
        this.props.approveHodlT(this.state.amount);
      }
    }
    this.props.hideModal();
    if (this.props.actionName === 'To HODLC') {
      return notify.success(t('wallet.notify.toHodlc', {amount:this.state.amount}))
    } else if( this.props.actionName === 'To HODLT') {
      return notify.success(t('wallet.notify.toHodlt', {amount:this.state.amount}))
    } else {
      const actionI18N = this.props.t(`wallet.notify.otherActions.action.${this.props.actionName.toLowerCase()}`);
      const unitsI18N = this.props.t(`wallet.notify.otherActions.units.${this.props.type.toLowerCase()}`)
      return notify.success(t('wallet.notify.otherActions.template', {
        action: actionI18N,
        amount: this.state.amount,
        units: unitsI18N
      }));
      // `You ${this.props.actionName} ${this.state.amount} ${this.props.type}, waiting for Ethereum to confirm.`
    }
  };
  renderConvert = () => {
    if(this.props.actionName === 'Approve'|| this.props.type === "eth"){
      return (" ")
    } else {
      return (<div className="field">
        <div className="ui huge transparent input sub-input">
        &#8776;<small>(estimated)</small>
          <input value={this.state.convertAmt} readOnly/>
          <div className="unit" valign="middle">
            HODLC
          </div>
        </div>
      </div>)
    } 
  }

  render() {
    return ReactDOM.createPortal(
      <div
        className="ui dimmer modals visible active"
        onClick={() => this.props.hideModal()}
      >
        <div
          onClick={(e) => e.stopPropagation()}
          className="ui small modal visible active main-modal"
        >
          <div className={`ui top attached big label modal-title`}>
            {this.props.actionName}
          </div>
          <div className="modal-body">
            <form className="ui form" onSubmit={this.onSubmit}>
              <div className="input-label">
                {this.props.type === 'eth'? this.props.type.toUpperCase():'HODLT'} amount:
              </div>
              <input className="modal-input" placeholder='Desired amount' onChange={(e) => this.setState({ amount: e.target.value, convertAmt:e.target.value })} />
              <div className='equivalent-container'>
                {this.renderConvert()}
              </div>
              <div className='modal-button-group'>
                <button className="ui button bg-modal-button cancel" onClick={() => this.props.hideModal()} >Cancel</button>
                <button className="ui button bg-modal-button highlight" type="submit" >{`Confirm on`}<img alt='metamask' className='metamask-icon' src='https://cdn.worldvectorlogo.com/logos/metamask.svg' width='15px'/></button>
              </div>
            </form>
          </div>
        </div>
      </div>,
      document.querySelector("#modal")
    );
  }
}

const mapStateToProps = ({ wallet }) => {
  return {
    eth: wallet.eth,
    hodlc: wallet.hodlc,
    hodlt: wallet.hodlt,
    redeemPower:wallet.redeemPower,
    ethinUSD:wallet.ethinUSD,
    hodlcinUSD:wallet.hodlcinUSD
  };
};
export default connect(mapStateToProps, {
  depositEth,
  withdrawEth,
  hodlTIssue,
  hodlTRedeem,
  approveHodlT
})(withTranslation()(Modal));
