import React, { Component } from 'react'
import Web3 from 'web3'
import { connect } from 'react-redux'

// Install Toastify
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

import { getUserBalances, getDexData } from '../actions'
import Dashboard from './Dashboard'
import SideNav from './Navs/Navigator'
import connector from '../connector'
import TopNav from './Navs/Navbar'
import config from '../config.js'

class App extends Component {
  componentWillUnmount () {
    clearInterval(this.myinterval)
    clearInterval(this.myinterval2)
  }

  componentDidMount () {
    // init
    this.props.getUserBalances()
    this.props.getDexData()
    try {
      this.myinterval = setInterval(() => {
        this.props.getUserBalances()
      }, config.rqstInteval.balance) // refresh wallet balance every 10 seconds
    } catch (e) {
      console.log(e)
    }
    try {
      this.myinterval2 = setInterval(() => {
        this.props.getDexData()
      }, config.rqstInteval.dexData) // request dex data every 5 minutes
    } catch (e) {
      console.log(e)
    }
  }

  async componentWillMount () {
    await this.injectWeb3()
    await this.checkMetaMask()
    await connector.getDexData()
  }

  async injectWeb3 () {
    try {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum)
        // await window.ethereum.enable();
      } else if (
        window.web3 &&
        window.web3.currentProvider &&
        (window.web3.currentProvider.isMetaMask ||
          window.web3.currentProvider.isMist)
      ) {
        window.web3 = new Web3(window.web3.currentProvider)
      } else {
        // window.alert(
        //   "Metamask or Mist is not detected. You should consider trying MetaMask or Mist!"
        // );
      }
    } catch (e) {
      console.log(JSON.stringify(e))
    }
  }

  async checkMetaMask () {
    const web3 = window.web3
    const accounts = web3 ? await web3.eth.getAccounts() : {}
    if (accounts.length === 0) {
      // window.alert("Metamask is locked.");
      // notify.error("Reload the page and connect with Metamask/Mist.");
    }
  }

  render () {
    return (
      <div>
        <SideNav />
        <TopNav />
        <Dashboard />
        <ToastContainer
          position='bottom-right'
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
      </div>
    )
  }
}

export default connect(null, { getUserBalances, getDexData })(App)
