import config from '../config'
import BigNumber from 'bignumber.js'

import ten from './ten'

const COLD_PERIOD_FOR_REFRESHING_PRICING = config.COLD_PERIOD_FOR_REFRESHING_PRICING

async function checkEthInfo (web3) {
  const setMarketInfo = async (web3) => {
    const instance = config.HodlDex(web3)
    if (instance) {
      const v = await instance.methods.convertEthToUsd(web3.utils.toWei('1', 'ether')).call()
      const r = web3.utils.fromWei(v, 'ether')
      localStorage.setItem('eth_in_usd', `${r}@${Date.now()}`)
    }
  }
  let local = localStorage.getItem('eth_in_usd')
  if (!local) {
    await setMarketInfo(web3)
  } else {
    local = local.split('@')
    const timestamp = local[1]
    const period = Date.now() - timestamp
    if (period > COLD_PERIOD_FOR_REFRESHING_PRICING) { // 3 minute
      await setMarketInfo(web3)
    }
  }
}

async function checkHodlInfo (web3) {
  const setMarketInfo = async (web3) => {
    const instance = config.HodlDex(web3)
    if (instance) {
      const r = await instance.methods.rates().call()
      const tmp = new BigNumber(r.hodlUsd)
      const rate = tmp.dividedBy('1000000000000000000').decimalPlaces(4).toNumber()
      localStorage.setItem('hodlc_in_usd', `${rate}@${Date.now()}`)
    }
  }
  let local = localStorage.getItem('hodlc_in_usd')
  if (!local) {
    await setMarketInfo(web3)
  } else {
    local = local.split('@')
    const timestamp = local[1]
    const period = Date.now() - timestamp
    if (period > COLD_PERIOD_FOR_REFRESHING_PRICING) { // 3 minute
      await setMarketInfo(web3)
    }
  }
}

async function checkHodlToEthInfo (web3) {
  const setMarketInfo = async (web3) => {
    const instance = config.HodlDex(web3)
    if (instance) {
      const r = await await instance.methods.convertHodlToEth(ten.more('1')).call()
      const rate = web3.utils.fromWei(r, 'ether')
      localStorage.setItem('hodlc_in_eth_info', `${rate}@${Date.now()}`)
    }
  }

  let local = localStorage.getItem('hodlc_in_eth_info')
  if (!local) {
    await setMarketInfo(web3)
  } else {
    local = local.split('@')
    const timestamp = local[1]
    const period = Date.now() - timestamp
    if (period > COLD_PERIOD_FOR_REFRESHING_PRICING) { // 3 minute
      await setMarketInfo(web3)
    }
  }
}

const converters = {
  convertEthToUsd: async (web3, amount = 0) => {
    await checkEthInfo(web3)
    if (localStorage.getItem('hodlc_in_usd')) {
      const rate = localStorage.getItem('eth_in_usd').split('@')[0]
      const result = amount * rate
      return result
    }
  },
  convertHodlToUsd: async (web3, amount) => {
    await checkHodlInfo(web3)
    if (localStorage.getItem('hodlc_in_usd')) {
      const latest_price = localStorage.getItem('hodlc_in_usd').split('@')[0]
      const tmp = new BigNumber(amount)
      const result = tmp.multipliedBy(latest_price).decimalPlaces(2).toNumber()
      return result
    }
  },
  ten,
  convertHodlToEth: async (web3, hodlcAmount) => {
    await checkHodlToEthInfo(web3)
    if (localStorage.getItem('hodlc_in_eth_info')) {
      const latest_price = localStorage.getItem('hodlc_in_eth_info').split('@')[0]
      const tmp = new BigNumber(hodlcAmount)
      const result = tmp.multipliedBy(latest_price).decimalPlaces(18).toNumber()
      return result
    }
  }
}

export default converters
